*,
*::before,
*::after {
    box-sizing: border-box;
}

html {
    overflow: auto;
}
body {
    overflow: hidden;
}

.container {
    width: 80%;
    max-width: 1000px;
    margin: 0 auto;
    text-align: center;
}

a {
    text-decoration: none;
    color: black;
    font-weight: 600;
}
